export function formatDate(str: string): string {
  if (!str) return "";
  const date: Date = new Date(str);
  const time: number = new Date().getTime() - date.getTime(); //Áé∞Âú®ÁöÑÊó∂Èó¥-‰º†ÂÖ•ÁöÑÊó∂Èó¥ = Áõ∏Â∑ÆÁöÑÊó∂Èó¥ÔºàÂçï‰Ωç = ÊØ´ÁßíÔºâ
  let result!: string;
  switch (true) {
    case time < 0:
      result = "";
      break;
    case time >= 0 && time / 1000 < 30:
      result = "ÂàöÂàö";
      break;
    case time / 1000 >= 30 && time / 1000 < 60:
      result = Math.round(time / 1000).toString() + "ÁßíÂâç";
      break;
    case time / 1000 >= 60 && time / 60000 < 60:
      result = Math.round(time / 60000).toString() + "ÂàÜÈíü";
      break;
    case time / 60000 >= 60 && time / 3600000 < 24:
      result = Math.round(time / 3600000).toString() + "Â∞èÊó∂Ââç";
      break;
    case time / 3600000 >= 24 && time / 86400000 < 31:
      result = Math.round(time / 86400000).toString() + "Â§©Ââç";
      break;
    case time / 86400000 >= 31 && time / 2592000000 < 12:
      result = Math.round(time / 2592000000).toString() + "ÊúàÂâç";
      break;
    default:
      result = Math.round(time / 31536000000).toString() + "Âπ¥Ââç";
  }
  return result;
}
