            <p class="time">
              Ê≥®ÂÜåÊó∂Èó¥Ôºö
              {formatDate(this.userInfo.create_at)}
            </p>
          </section>
          <div class="replies">
            <p>ÂõûÂ§çÁöÑ‰∏ªÈ¢ò</p>
            <ul>
              {this.mapListAndReturnElement(this.userInfo.recent_replies)}
            </ul>
          </div>
          <div class="topics">
            <p>ÂàõÂª∫ÁöÑ‰∏ªÈ¢ò</p>
            <ul>{this.mapListAndReturnElement(this.userInfo.recent_topics)}</ul>
          </div>
        </div>
      </div>
    );
  }

  private loginName = "";
  private userInfo: UserInfoEntity = emptyUserInfo();

  public mounted() {
